















































import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import Layout from "@/components/Layout.vue";
import axios from "axios";
import "@/../node_modules/vant/lib/popup/index.css";
import "@/../node_modules/vant/lib/popup/style";
import NumberPad from "../Money/NumberPad.vue";

@Component({ components: { Layout, NumberPad } })
export default class BudgetSetting extends Vue {
  period = "day";
  periodName = "日";
  budgetId = "";
  time = `${new Date().getFullYear()}.${
    new Date().getMonth() + 1
  }.${new Date().getDate()}`;
  totalCount = 0;
  aviCount = 0;
  overCount = 0;
  columns = ["日", "周", "月", "年"];
  popVi = false;

  mounted() {
    this.getBudget();
  }
  handleBack() {
    this.$router.go(-1);
  }

  onUpdateAmount(value: string) {
    this.totalCount = Number(value);
  }

  updateBudget(v: string) {
    axios
      .post("http://localhost:3000/api/account/update/budget", {
        userId: this.$store.state.userId,
        budgetId: this.budgetId,
        budget: v,
      })
      .then((result) => {
        //
      })
      .catch((err) => {
        //
      });
  }

  getBudget() {
    axios
      .post("http://localhost:3000/api/account/fetch/budget", {
        userId: this.$store.state.userId,
        time: this.time,
        period: this.period,
      })
      .then((result: any) => {
        console.log(result);
        this.budgetId = result.data.data?.budgetId;
      })
      .catch((err) => {
        console.log(err);
      });
  }
  jumpPrevious() {
    let date, day, month, year;
    switch (this.period) {
      case "day":
        date = new Date(
          new Date(this.time.split(".").join("-")).getTime() -
            1000 * 60 * 60 * 24
        );
        year = date.getFullYear();
        month = date.getMonth() + 1;
        day = date.getDate();
        this.time = `${year}.${month}.${day}`;
        break;
      case "week":
        this.time = this.getWeekTime(
          new Date(
            new Date(this.time.split("-")[0].split(".").join("-")).getTime() -
              7 * 1000 * 60 * 60 * 24
          )
        );
        break;
      case "month":
        this.time =
          this.time.split(".")[1] === "1"
            ? `${Number(this.time.split(".")[0]) - 1}.12`
            : `${this.time.split(".")[0]}.${
                Number(this.time.split(".")[1]) - 1
              }`;
        break;
      case "year":
        this.time = (Number(this.time) - 1).toString();
        break;
    }
  }
  jumpNext() {
    let date, day, month, year;
    switch (this.period) {
      case "day":
        date = new Date(
          new Date(this.time.split(".").join("-")).getTime() +
            1000 * 60 * 60 * 24
        );
        year = date.getFullYear();
        month = date.getMonth() + 1;
        day = date.getDate();
        this.time = `${year}.${month}.${day}`;
        break;
      case "week":
        this.time = this.getWeekTime(
          new Date(
            new Date(this.time.split("-")[0].split(".").join("-")).getTime() +
              7 * 1000 * 60 * 60 * 24
          )
        );
        break;
      case "month":
        this.time =
          this.time.split(".")[1] === "12"
            ? `${Number(this.time.split(".")[0]) + 1}.1`
            : `${this.time.split(".")[0]}.${
                Number(this.time.split(".")[1]) + 1
              }`;
        break;
      case "year":
        this.time = (Number(this.time) + 1).toString();
        break;
    }
  }

  onConfirm(v: string) {
    switch (v) {
      case "日":
        this.period = "day";
        this.time = `${new Date().getFullYear()}.${
          new Date().getMonth() + 1
        }.${new Date().getDate()}`;
        break;
      case "周":
        this.period = "week";
        this.time = this.getWeekTime(new Date());
        break;
      case "月":
        this.period = "month";
        this.time = `${new Date().getFullYear()}.${new Date().getMonth() + 1}`;
        break;
      case "年":
        this.period = "year";
        this.time = `${new Date().getFullYear()}`;
        break;
    }
    this.periodName = v;
    this.popVi = false;
  }

  getWeekTime(date: any) {
    const week = date.getDay();

    const startTime =
      week === 0
        ? date.getTime() - 6 * 1000 * 60 * 60 * 24
        : date.getTime() - (week - 1) * 1000 * 60 * 60 * 24;
    const endTime =
      week === 0
        ? date.getTime()
        : date.getTime() + (7 - week) * 1000 * 60 * 60 * 24;

    return `${new Date(startTime).getFullYear()}.${
      new Date(startTime).getMonth() + 1
    }.${new Date(startTime).getDate()}-${new Date(endTime).getFullYear()}.${
      new Date(endTime).getMonth() + 1
    }.${new Date(endTime).getDate()}`;
  }
  onCancel() {
    this.popVi = false;
  }
  onChange() {}

  @Watch("time")
  fetchBudget() {
    this.getBudget();
  }
  periodSelect() {
    this.popVi = true;
  }
}
